/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-multiple-blocks-gb-button {
	&.block-editor-block-list__block.wp-block {
		max-width: 1140px;
	}
}

.wp-block-multiple-blocks-gb-button
	> .wp-block-multiple-blocks-gb-button__wrapper
	> .wp-block-multiple-blocks-gb-button__btn
	> .wp-block-multiple-blocks-gb-button__btn-text
	> a:focus {
	outline: none;
}

.wp-block-multiple-blocks-gb-button
	> .wp-block-multiple-blocks-gb-button__wrapper
	> .wp-block-multiple-blocks-gb-button__btn
	> .wp-block-multiple-blocks-gb-button__btn-text {
	padding: 0 72px;
	outline: none;
	box-shadow: none;
	text-decoration: none;
}

.components-button.wp-block-multiple-blocks-gb-button__btn:focus {
	outline: none;
	box-shadow: none;
	color: #fff;
}

.wp-block-multiple-blocks-gb-button__input {
	border: 1px solid #777;
}

.wp-block-multiple-blocks-gb-button__input > .components-base-control__field {
	margin-bottom: 0;
}

.wp-block-multiple-blocks-gb-button__input
	> .components-base-control__field
	> .block-editor-url-input__input {
	width: 100%;
}
